.form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 400px;
  height: 420px;
  margin: 40px;
  background-color: rgb(226, 217, 217);
  box-shadow: 4px 4px 15px 0px #000000;
  transition: 1s;
  z-index: 1;
}
.form:hover {
transform: scale(1.1);
}
.button {
  width: 200px;
}
.h2 {
  font-size: 14px;
}