.modal {
  position: absolute;
  font-weight: 600;
  width: 300px;
  height: 80px;
  font-size: 20px;
  top: 500px;
  left: 100px;
  background-color: rgb(195, 235, 127);
  top:200px;
  z-index: 999;
  text-align: center;
  padding: 14px 6px;
  border: 1px solid black;
  box-shadow: 4px 4px 15px 0px #000000;
}


.closeButton{
  background-color: rgb(247, 252, 247);
  border: 1px solid rgb(59, 58, 58);
  margin-top: 14px;
  box-shadow: 4px 4px 15px 0px #000000;
  transition: 1s;
}

.visualyHidden {
visibility: hidden;
opacity: 0;
}

.text {
  margin: 0;
}